import React, { Component } from 'react';
import Link from 'next/link';
import styled from 'styled-components';
import HamburgerMenu from 'react-hamburger-menu';
import ColouredLink from './styles/ColouredLink';

const NavBar = styled.div`
	position: fixed;
	top: 2.5rem;
	right: 3rem;
	.nav {
		@media (max-width: 767px) {
			display: none;
		}
	}
`;

const HamburgerMenuContainer = styled.div`
	cursor: pointer;
	position: absolute;
	top: 0.7rem;
	right: 3rem;
	@media (max-width: 767px) {
		right: 0;
	}
	@media (min-width: 768px) {
		display: none;
	}
`;

export default class Nav extends Component {
	constructor() {
		super();
		this.state = {
			open: [ true, false, true, false ]
		};
	}
	handleClick(id) {
		let { open } = this.state;
		this.setState({
			open: [ ...open.slice(0, id), !open[id], ...open.slice(id + 1) ]
		});
	}

	render() {
		return (
			<NavBar>
				<div className="nav">
					<Link href="/">
						<ColouredLink className="home__nav">Home</ColouredLink>
					</Link>
					<Link href="/about">
						<ColouredLink className="about__nav">About</ColouredLink>
					</Link>
					<Link href="/projects">
						<ColouredLink className="projects__nav">Projects</ColouredLink>
					</Link>
					<Link href="/contact">
						<ColouredLink className="contact__nav">Contact</ColouredLink>
					</Link>
				</div>
				<HamburgerMenuContainer className="menu-row">
					<HamburgerMenu
						className="hamburger-menu"
						isOpen={this.state.open[3]}
						menuClicked={this.handleClick.bind(this, 3)}
						width={30}
						height={19}
						strokeWidth={1}
						rotate={0}
						color="white"
						borderRadius={0}
						animationDuration={0.2}
					/>
				</HamburgerMenuContainer>
			</NavBar>
		);
	}
}
